import { useState } from "react";
import woman from './Images/blackwoman.png';
import './App.css';



function App() {
  
  const [count, setCount] = useState(0)
  const handleCount = () => {
    setCount(count + 1)
  }

  const [data, setData] = useState("My Name is Dara")
  
  const handleName = () => {
    setData ("My Name is Dara")
  }
  const handleSkills = () => {
    setData ("I'm a Frontend Developer")
  }

  return (
    
    <main className="container">
      
      <div className="woman item">
        <img src={woman} width="150" height="150" alt="black woman icon"/>
      </div>
      <div className="item item-2">
        <h1>Hi!</h1>
      </div>
      <div className="item item-3">
        <p>{data}</p>
      </div>
      <div className="item item-4">
        <button onClick={handleName}>Name</button>
        <button onClick={handleSkills}>Skills</button>
      </div>
      <footer>
        <div className ="heart">
          <p class="far">{count}</p>
          <i onClick={handleCount} class="far fa-heart fa-2x"></i>

        </div>
        <div className="logos">
          <a href="https://www.instagram.com/__darai/" target="_blank">
          <i class="fab fa-instagram fa-2x"></i>
          </a>
          <a href="https://www.linkedin.com/in/darai/" target="_blank">
          <i class="fab fa-linkedin fa-2x"></i>
          </a>
        </div>
      </footer>
      </main>
      
  );
}

export default App;
